import { Container, Grid, Image, Item, Button } from "semantic-ui-react";
import { useState } from "react";
import imageCompression from "browser-image-compression";
import './App.css';

function App() {
  const [origImage, setOrigImage] = useState("");
  const [origImageFile, setOrigImageFile] = useState("");
  const [compressImage, setCompressImage] = useState("");
  const [filename, setFilename] = useState("");
  const handle = (e) => {
    const imagefile = e.target.files[0];
    setOrigImage(imagefile);
    setOrigImageFile(URL.createObjectURL(imagefile));
    setFilename(imagefile.name);
  };

  const handleCompressImage = (e) => {
    e.preventDefault();

    const options = {
      maxSizeMB: 1,
      maxWidthOrHeight: 500,
      useWebWorker: true,
    };

    if (options.maxSizeMB >= origImage / 1024) {
      alert("Image is too small to compress");
      return 0;
    }
    let output;
    imageCompression(origImage, options).then((x) => {
      output = x;

      const downloadLink = URL.createObjectURL(output);
      setCompressImage(downloadLink);

    })
  }

  return (
    <div className="App">
      <Container>
        <Grid>
          <Grid.Column width={6}>
            <Item>
              {origImageFile ? (
                <Image src={origImageFile} />
              ) : (
                <Image src="data:image/png;base64," />
              )}
              <input onChange={(e) => handle(e)} type="file" accept="image/*" className="mt-2 " />
            </Item>
          </Grid.Column>
          <Grid.Column width={4}>
            <h1></h1>
            {origImageFile && <Button primary onClick={(e) => handleCompressImage(e)} >Compress</Button>}
          </Grid.Column>
          <Grid.Column width={6}>
            <Item>
              {compressImage ? (
                <Image src={compressImage} />
              ) : (
                <Image src="data:image/png;base64," />
              )}
              {compressImage && <Button color="white" >
                <a href={compressImage} download={filename} >
                  {" "}
                  Download
                </a>
              </Button>}
            </Item>
          </Grid.Column>
        </Grid>
      </Container>

    </div>
  );
}

export default App;